export class IlRegionQuery{
    //统计口径  1.发货 2.收货
    caliber:number;
    //选中的 1.分区销量2.分区单量 3.区域销量4.区域单量
    selectTab:number;
    //区域销量中 选中查看的区域名称
    selectRegion:string;
    //部门编码
    deptNo:string;

    constructor(){
        this.caliber = 1;
        this.selectTab = 1;
        this.selectRegion='';
        this.deptNo = '';
    }

    Caliber(caliber :number):IlRegionQuery{
        this.caliber = caliber;
        return this;
    }

    SelectTab(selectTab:number):IlRegionQuery{
        this.selectTab = selectTab;
        return this;
    }

    SelectRegion(selectRegion:string):IlRegionQuery{
        this.selectRegion = selectRegion;
        return this;
    }

    DeptNo(deptNo:string):IlRegionQuery{
        this.deptNo = deptNo;
        return this;
    }

}


